import * as okayapi from '../../../components/okayapi/okayapi.js';


export default {
	data() {
		return {
			title: 'Common_Password',
			items: [{
					value: 'default',
					name: '默认'
				},
				{
					value: 'number',
					name: '较低',
				},
				{
					value: 'normal',
					name: '正常',
				},
				{
					value: 'strong',
					name: '强',
				},
				{
					value: 'extreme',
					name: '极强',
				},
			],
			min_len: 6,
			max_len: 10,
			current: 0,
			first: '',
		}
	},
	methods: {
		passwordOkay: function() {
			var params = {
				s: "App.Common_Password.RandOne", // 必须，待请求的接口服务名称
				level: this.items[this.current].value,
				min_len: this.min_len,
				max_len: this.max_len,
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					console.log(res);
					this.first = res.data.data.password
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});
		},
		radioChange: function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		},
		sliderChange(e) {
			this.min_len = e.detail.value;
			this.passwordOkay();
		},
		sliderChangeMax(e) {
			this.max_len = e.detail.value;
			this.passwordOkay();
		},
	}
}
